<template>
  <div>
    <!--  -->
    <el-upload v-if="multiple" action='string' list-type="picture-card" :on-preview="handlePreview" :auto-upload="false" :on-remove="handleRemove" :http-request="upload" :on-change="consoleFL" :file-list="uploadList">
      <i class="el-icon-plus"></i>
    </el-upload>
    <!--  -->
    <el-upload v-else class="avatar-uploader" action="'string'" :auto-upload="false" :show-file-list="false" :on-change="handleCrop" :http-request="upload">
      <img v-if="imageUrl" :src="imageUrl" class="avatar" ref="singleImg" @mouseenter="mouseEnter" @mouseleave="mouseLeave" :style="{width:width+'px',height:height+'px'}">
      <i v-else class="el-icon-plus avatar-uploader-icon" :style="{width:width+'px',height:height+'px','line-height':height+'px','font-size':height/6+'px'}"></i>
      <!--  -->
      <!-- <div v-if="imageUrl" class="reupload" ref="reupload" @click.stop="handlePreviewSingle" @mouseenter="mouseEnter" @mouseleave="mouseLeave" :style="{width:reuploadWidth+'px',height:reuploadWidth+'px','line-height':reuploadWidth+'px','font-size':reuploadWidth/5+'px'}"></div> -->
      <div id="uploadIcon" v-if="imageUrl" ref="reupload" @mouseenter="mouseEnter" @mouseleave="mouseLeave" :style="{width:'100%'}">
        <i class="el-icon-zoom-in" @click.stop="handlePreviewSingle" :style="{color:'#2E2E2E',fontSize:'25px',display:'inline-block',paddingRight:'15px'}"></i>
        <i class="el-icon-upload" :style="{color:'#2E2E2E',fontSize:'25px',display:'inline-block'}"></i>
      </div>
      <div class="reupload" ref="uploading" :style="{width:reuploadWidth+'px',height:reuploadWidth+'px','line-height':reuploadWidth+'px','font-size':reuploadWidth/5+'px'}">..</div>
      <div class="reupload" ref="failUpload" :style="{width:reuploadWidth+'px',height:reuploadWidth+'px','line-height':reuploadWidth+'px','font-size':reuploadWidth/5+'px'}"></div>
    </el-upload>
    <!--  -->
    <el-dialog :visible.sync="dialogVisible">
      <img width="100%" :src="dialogImageUrl" alt="">
    </el-dialog>
    <!--  -->
    <el-dialog :visible.sync="cropperModel" width="800px" :before-close="beforeClose">
      <Cropper :img-file="file" ref="vueCropper" :fixedNumber="fixedNumber" @upload="upload" >
      </Cropper>
    </el-dialog>
  </div>
</template>
<script>
import Cropper from './cropper'

export default {
  name: 'uploader',
  props: {
    targetUrl: {
      // 
      type: String,
      default: '/api/postPhoto'
    },
    multiple: {
      // 
      type: Boolean,
      default: false
    },
    initUrl: {
      // 
      default: ''
    },
    fixedNumber: {
      // 
      default: function () {
        return [16, 9]
      }
    },
    width: {
      // 
      type: Number,
      default: 300
    },
    height: {
      // 
      type: Number,
      default: 300
    }
  },
  data () {
    return {
      file: '', // 
      imageUrl: '', // 
      dialogImageUrl: '', // 
      uploadList: [], // 
      reupload: true, // ""
      dialogVisible: false, // 
      cropperModel: false, // 
      reuploadWidth: this.height * 0.7 // 
    }
  },
  updated () {
    if (this.$refs.vueCropper) {
      this.$refs.vueCropper.Update()
    }
  },
  watch: {
    initUrl: function (val) {
      // 
      // console.info('watch')
      if (val) {
        if (typeof this.initUrl === 'string') {
          this.imageUrl = val
        } else {
          this.uploadList = this.formatImgArr(val)
        }
      }
    }
  },
  mounted () {
    if (typeof this.initUrl === 'string') {
      this.imageUrl = this.initUrl
    } else {
      this.uploadList = this.formatImgArr(this.initUrl)
      console.log(this.uploadList,'11')
    }
  },
  methods: {
    /** **************************** multiple **************************************/
    handlePreview (file) {
      // 
      this.dialogImageUrl = file.url
      this.dialogVisible = true
    },
    handleRemove (file, fileList) {
      // 
      this.uploadList = fileList
      this.$emit('imgupload', this.formatImgArr(this.uploadList))
      this.$emit('getRawFile',this.formatImgArr(this.uploadList))
    },
    consoleFL (file, fileList) {
      // 
      this.cropperModel = true
      this.file = file
      this.uploadList = fileList
    },
    /************************************************************************************/

    /****************************** single **************************************/
    handlePreviewSingle (e) {//
      this.dialogImageUrl = this.imageUrl
      this.dialogVisible = true
    },
    mouseEnter () {//
      this.$refs.reupload.style.display = 'block'
      if (this.$refs.failUpload.style.display === 'block') {
        this.$refs.failUpload.style.display = 'none'
      }
      this.$refs.singleImg.style.opacity = '0.6'
    },
    mouseLeave () {
      // 
      this.$refs.reupload.style.display = 'none'
      this.$refs.singleImg.style.opacity = '1'
    },
    handleCrop (file, files) {
      // 
      console.log(this.cropperModel,this)
      this.cropperModel = true
      this.file = file
      // this.imageUrl = file.url
    },
    /************************************************************************************/

    upload (data) {
      // upload
      if (!this.multiple) {
        // 
        this.$refs.uploading.style.display = 'block'
      }
      let formData = new FormData()
      formData.append('photo', data)
      
      
      this.$proxy.post(this.targetUrl, formData).then(res => {
        if (!this.multiple) {
          // 
          this.$refs.uploading.style.display = 'none'
        }
        if (res.data === 'success') {
          // ()
          // const currentPic = res.data.url
          const currentPic=URL.createObjectURL(data);
          if (this.multiple) {
            this.uploadList.push({
              url: currentPic,
              uid: '111',
              data
            })
            this.uploadList.pop()
            this.$emit('imgupload', this.formatImgArr(this.uploadList,data))
            this.$emit('getRawFile',this.formatImgArr(this.uploadList,data))
          } else {
            this.$emit('imgupload', currentPic)
            this.$emit('getRawFile',data)
            this.imageUrl=currentPic
          }
        } else {
          // 
          if (!this.multiple) {
            this.$refs.failUpload.style.display = 'block'
          } else {
            this.uploadList.pop()
          }
        }
      })
      this.cropperModel = false
    },
    formatImgArr (arr) {
      const result = arr.map((item, index) => {
        // console.log(typeof item,item)
        if (typeof item === 'string') {
          return {
            url: item,
            uid: `index${index}`
          }
        } else {
          return {url:item.url,data:item}
        }
      })
      return result
    },
    beforeClose (done) {
      this.uploadList.pop()
      this.cropperModel = false
    }
  },
  components: {
    Cropper
  }
}
</script>
<style>
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  color: #8c939d;
  text-align: center;
}
.avatar {
  display: block;
}
.reupload {
  border-radius: 50%;
  position: absolute;
  color: #fff;
  background-color: #000000;
  opacity: 0.6;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: none;
}
#uploadIcon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: none;
}
</style>





